window.jQuery=function(selectorOrArray){
    let elements
    // const Â£∞ÊòéÂèòÈáèÂøÖÈ°ªËµãÂÄº
    if(typeof selectorOrArray==='string'){
       elements=document.querySelectorAll(selectorOrArray)//Êï∞ÁªÑ
        //    apiÂØπË±°ÂèØ‰ª•Êìç‰Ωúelements
    }else if(selectorOrArray instanceof Array){
        elements=selectorOrArray
    }

   return{
       oldApi:selectorOrArray.oldApi,
 //    Èó≠ÂåÖÔºåÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÂèòÈáè„ÄÇaddClass()ËÆøÈóÆelements
        addClass(className){
            for(let i=0;i<elements.length;i++){
                elements[i].classList.add(className)
            }
            return this //thisÊòØapi
        },
        find(selector){
            let array=[]
            for(let i=0;i<elements.length;i++){
                const elements2=Array.from(elements[i].querySelectorAll(selector))
                array=array.concat(elements2)
                // elements[i].querySelectorAll(selector)‰º™Êï∞ÁªÑÔºåArray.from()ËΩ¨Âåñ‰∏∫ÁúüÊï∞ÁªÑ
            }
        //    const newApi=jQuery(array)
        //    return newApi
        array.oldApi=this//thisÂ∞±ÊòØapi  Êóßapi
        return jQuery(array)
        },
        each(fn){
            for(let i=0;i<elements.length;i++){
               fn.call(null, elements[i],i)
            }
            return this
        },
        parent(){
            const array=[]
            this.each((node)=>{
                if(array.indexOf(node.parentNode)===-1){
                    array.push(node.parentNode)
                }else{
                }
            })
            return jQuery(array)
        },
        children(){
            const array=[]
            this.each((node)=>{
                array.push(...node.children)//Â±ïÂºÄÊìç‰ΩúÁ¨¶ÔºåÁõ∏ÂΩì‰∫é array.push(node.children[0],node.children[1],node.children[2]...)
            })
            return jQuery(array)
        },
        print(){
            console.log(elements)
        },
        end(){
            return this.oldApi//this ÊòØÂΩìÂâçÁöÑapi (api2) Êñ∞ÁöÑapi
        }
    }
 }